.App{
  padding: 0;
  margin:0;
  width: 100vw;
  height: 100vh;
  font-family: 'Quicksand', sans-serif;
}

.Title {
  display: grid;
  grid-template-columns: 50vw 50vw;
  grid-template-rows: 15vh;
}

h1{
  grid-column-start: 1;
  width: 50vw;
  font-size: 3em;
  align-self: center;
  padding-left: 2vw;
}

.CardList {
  display: flex;
  width: 100vw;
  flex-direction: row;
  flex-wrap: wrap;
}

button {
  background: none;
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5em;
  margin: 0 1vw;
  border:none;
  text-decoration: none;
}

button:hover {
    font-weight: 600;
}


button:focus {
  outline:none; 
  font-weight: 600;
}

.Filters {
  grid-column-start: 2;
  align-self: center;
  width: 50vw;
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width: 800px) {
  .Title {
    grid-template-columns: 100vw;
    grid-template-rows: 10vh;
    padding-bottom: 2vh;
  }

  h1{
    font-size: 2.5em;
    grid-column-start: 1;
    width: 100vw;
    text-align: center;
 }

  .Filters {
    grid-column-start: 1;
    width: 100vw;
    justify-content: center;
  }
}